@use "../../scss/abstract/index" as *;

.hero {
   width: 100%;
   position: relative;
   padding-top: 10rem;
   @include media(tablet-l) {
      padding-top: 0;
   }
   padding-bottom: 10rem;

   &__container {
      @include container();
      @include flex-column();
      background: white;
      padding: $g-elem $g-elem-sm;
      border-radius: 20px;
      box-shadow: $box-shadow-dark;

      &--growth-inner-bg {
         background: transparent;
         background-image: url("../../assets/imgs/growth-page/hero/hero-bg-inner.png");
         background-repeat: no-repeat;
         background-size: 600%, cover;
         background-position: 10% 80%;
         color: white;
         @include media(tablet-l) {
            background-position: center;
            background-size: contain, cover;
            box-shadow: none;
            padding: 5% 8% 1% 7%;
         }
      }

      &--working-inner-bg {
         background: transparent;
         background-image: url("../../assets/imgs/ways-working-page/hero/hero-bg-inner.png");
         background-repeat: no-repeat;
         background-size: 600%, cover;
         background-position: 10% 80%;
         color: white;
         @include media(tablet-l) {
            background-position: center;
            background-size: contain, cover;
            box-shadow: none;
            padding: 2% 8% 2% 8%;
         }
      }
   }

   &__heading {
      margin-bottom: $g-elem;
   }

   &__paragraph {
      position: relative;
      font-size: clamp(2rem, 3vw, 4rem);
      line-height: 1.6;
      @include media(tablet-l) {
         font-size: clamp(1.2rem, 2vw, 3.5rem);
      }
      font-weight: 700;
      margin-top: 4rem;
      margin-bottom: 0;
      @include media(tablet-l) {
         margin-top: 0;
      }
      &::before {
         content: "";
         position: absolute;
         top: -12%;
         width: 12%;
         height: 22%;
         background: url(../../assets/global/quotation_mark_before.svg)
            no-repeat center;
         background-size: contain;
         margin-left: -2rem;
      }
      &::after {
         content: "";
         position: absolute;
         bottom: -8%;
         width: 12%;
         height: 22%;
         background: url(../../assets/global/quotation_mark_before.svg)
            no-repeat center;
         background-size: contain;
         margin-left: -2rem;
      }
   }

   &__text {
      display: flex;
      justify-content: center;
      flex-direction: column;
      text-align: start;
      padding: clamp(3rem, 3vw, 6rem);
   }

   @include media(tablet-l) {
      &__container {
         // padding: $g-elem-ml;
         flex-direction: row;
      }

      .video-container {
         flex-basis: 50%;
         order: 1;
      }

      &__text {
         flex-basis: 50%;
      }
   }
}

.background-image {
   position: absolute;
   width: 100%;
   height: 100%;
   z-index: -1;
   background-size: auto, cover;
   background-repeat: no-repeat;
   @include media(tablet-l) {
      padding-top: 0;
      background-position: center right, center;
   }

   &--culture {
      @extend .background-image;
      background-image: url("../../assets/imgs/base-layout/avado-emblem.png"),
         url("../../assets/imgs/culture-page/hero/hero-bg.png");
      background-size: auto, 100% 100%;
   }

   &--join {
      @extend .background-image;
      background-image: url("../../assets/imgs/base-layout/avado-emblem.png"),
         url("../../assets/imgs/join-us-page/hero/hero-bg.png");
   }

   &--life {
      @extend .background-image;
      background-image: url("../../assets/imgs/base-layout/avado-emblem.png"),
         url("../../assets/imgs/working-life-page/hero/hero-bg.png");
   }

   &--growth {
      @extend .background-image;
      background-image: url("../../assets/imgs/base-layout/avado-emblem.png"),
         url("../../assets/imgs/growth-page/hero/hero-bg.png");
   }

   &--working {
      @extend .background-image;
      background-image: url("../../assets/imgs/base-layout/avado-emblem.png"),
         url("../../assets/imgs/ways-working-page/hero/hero-bg.png");
   }
}
